<template lang="pug">
    div.content-article-cate
        list-block  
            list-item(:link="`/user/setting/article_index?id=${item.id}&title=${item.title}`" :itemTitle="item.title" v-for="item in list" key="")
        p.license(v-text="site_license")

</template>
<script>
import listBlock from '@/components/listBlock'
import listItem from '@/components/listItem'
import { Toast } from 'mint-ui'
export default {
    name: 'settingArticleCate',
    components: {
        listBlock,
        listItem
    },
    data () {
        return {
            list: '',
            site_license: ''
        }
    },
    created () {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/settingsArticle_cate' : this.api.settingsArticle_cate();
            this.axios.get(axiosUrl).then(res => {
                if(res.data.status == 1) {
                    this.list = res.data.article_cates;
                    this.site_license = res.data.site_license;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
            .catch(err => {
                Toast(err || '请求失败');
            })
        }
    }
}
</script>
<style lang="less" scoped>
    @import '../../../assets/css/variable.less';
    .license{
        color: @fc-lighter;
        font-size: .6rem;
        text-align: center;
        margin-top: .5rem;
    }
</style>



